import React, { useState, useEffect } from 'react';
import { Server } from '../types';
import { getPublicServers, joinPublicServer } from '../services/api';
import { getAvatarColor } from '../utils/avatarColors';

interface PublicServersViewProps {
  onServerJoined: (server: Server) => void;
  userServerIds: string[];
}

const PublicServersView: React.FC<PublicServersViewProps> = ({
  onServerJoined,
  userServerIds,
}) => {
  const [servers, setServers] = useState<Server[]>([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState('');
  const [joining, setJoining] = useState<string | null>(null);

  useEffect(() => {
    loadServers();
  }, []);

  const loadServers = async (searchQuery?: string) => {
    setLoading(true);
    try {
      const data = await getPublicServers(searchQuery);
      setServers(data || []);
    } catch (error) {
      console.error('Failed to load public servers:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    loadServers(search);
  };

  const handleJoin = async (server: Server) => {
    if (joining) return;
    setJoining(server.id);
    try {
      await joinPublicServer(server.id);
      onServerJoined(server);
    } catch (error) {
      console.error('Failed to join server:', error);
    } finally {
      setJoining(null);
    }
  };

  const isAlreadyMember = (serverId: string) => userServerIds.includes(serverId);

  return (
    <div style={styles.container}>
      <div style={styles.header}>
        <div style={styles.headerContent}>
          <div style={styles.headerIcon}>
            <svg width="32" height="32" viewBox="0 0 24 24" fill="none">
              <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2z" stroke="currentColor" strokeWidth="2"/>
              <circle cx="12" cy="12" r="3" stroke="currentColor" strokeWidth="2"/>
              <path d="M12 2v4M12 18v4M2 12h4M18 12h4" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
            </svg>
          </div>
          <div>
            <h1 style={styles.title}> </h1>
            <p style={styles.subtitle}>    </p>
          </div>
        </div>
        <form style={styles.searchForm} onSubmit={handleSearch}>
          <div style={styles.searchInputWrapper}>
            <svg width="18" height="18" viewBox="0 0 24 24" fill="none" style={styles.searchIcon}>
              <circle cx="11" cy="11" r="7" stroke="currentColor" strokeWidth="2"/>
              <path d="M21 21l-4.35-4.35" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
            </svg>
            <input
              type="text"
              value={search}
              onChange={e => setSearch(e.target.value)}
              placeholder=" ..."
              style={styles.searchInput}
            />
          </div>
          <button type="submit" style={styles.searchBtn}>
            
          </button>
        </form>
      </div>

      <div style={styles.content}>
        {loading ? (
          <div style={styles.loading}>
            <div style={styles.loadingSpinner} />
            <span> ...</span>
          </div>
        ) : servers.length === 0 ? (
          <div style={styles.empty}>
            <svg width="64" height="64" viewBox="0 0 24 24" fill="none" style={{ marginBottom: 20, opacity: 0.4 }}>
              <circle cx="11" cy="11" r="7" stroke="currentColor" strokeWidth="1.5"/>
              <path d="M21 21l-4.35-4.35" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round"/>
            </svg>
            <h3 style={styles.emptyTitle}>  </h3>
            <p style={styles.emptyText}>
              {search ? '   ' : '   '}
            </p>
          </div>
        ) : (
          <div style={styles.grid}>
            {servers.map(server => (
              <div key={server.id} style={styles.card}>
                <div
                  style={{
                    ...styles.cardBanner,
                    backgroundImage: server.banner_url ? `url(${server.banner_url})` : undefined,
                    backgroundColor: server.banner_url ? undefined : getAvatarColor(server.id),
                  }}
                />
                <div style={styles.cardContent}>
                  <div style={styles.cardHeader}>
                    <div
                      style={{
                        ...styles.serverIcon,
                        backgroundImage: server.icon_url ? `url(${server.icon_url})` : undefined,
                        backgroundColor: server.icon_url ? undefined : getAvatarColor(server.id),
                      }}
                    >
                      {!server.icon_url && server.name[0]?.toUpperCase()}
                    </div>
                    <div style={styles.serverInfo}>
                      <div style={styles.serverName}>{server.name}</div>
                      <div style={styles.memberCount}>
                        <svg width="14" height="14" viewBox="0 0 24 24" fill="none">
                          <path d="M17 21v-2a4 4 0 00-4-4H5a4 4 0 00-4 4v2M9 11a4 4 0 100-8 4 4 0 000 8zM23 21v-2a4 4 0 00-3-3.87M16 3.13a4 4 0 010 7.75" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                        </svg>
                        {server.member_count || 0} 
                      </div>
                    </div>
                  </div>
                  <div style={styles.description}>
                    {server.description || ' '}
                  </div>
                  <button
                    style={{
                      ...styles.joinBtn,
                      ...(isAlreadyMember(server.id) ? styles.joinBtnDisabled : {}),
                    }}
                    onClick={() => handleJoin(server)}
                    disabled={isAlreadyMember(server.id) || joining === server.id}
                  >
                    {isAlreadyMember(server.id)
                      ? ' '
                      : joining === server.id
                        ? '...'
                        : ''}
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

