import React, { useState, useEffect, useRef } from 'react';
import { Channel, Message, User, WSMessagePayload, WSMessageDeletedPayload, WSReactionUpdatedPayload } from '../types';
import { getMessages, sendMessage, editMessage, deleteMessage, addReaction, removeReaction, uploadFile } from '../services/api';
import websocketService from '../services/websocket';
import { getAvatarColor } from '../utils/avatarColors';
import { debugLogger } from '../services/debugLogger';

const MAX_FILE_SIZE = 10 * 1024 * 1024; // 10 MB

const COMMON_EMOJIS = ['', '', '', '', '', '', '', ''];

interface ChatProps {
  channel: Channel;
  onUserClick?: (user: User, position: { x: number; y: number }) => void;
  initialMessage?: string;
  onInitialMessageUsed?: () => void;
}

const Chat: React.FC<ChatProps> = ({ channel, onUserClick, initialMessage, onInitialMessageUsed }) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [newMessage, setNewMessage] = useState('');
  const inputRef = useRef<HTMLTextAreaElement>(null);
  const [loading, setLoading] = useState(false);
  const [editingMessageId, setEditingMessageId] = useState<string | null>(null);
  const [editingContent, setEditingContent] = useState('');
  const [contextMenu, setContextMenu] = useState<{ x: number; y: number; messageId: string } | null>(null);
  const [emojiPicker, setEmojiPicker] = useState<{ messageId: string } | null>(null);
  const [hoveredMessageId, setHoveredMessageId] = useState<string | null>(null);
  const [replyingTo, setReplyingTo] = useState<Message | null>(null);
  const [sendButtonHovered, setSendButtonHovered] = useState(false);
  const [sendButtonActive, setSendButtonActive] = useState(false);
  const [attachedFile, setAttachedFile] = useState<File | null>(null);
  const [uploadProgress, setUploadProgress] = useState<number>(0);
  const [uploading, setUploading] = useState(false);
  const [attachmentMenu, setAttachmentMenu] = useState<{ x: number; y: number; messageId: string; url: string; isOwner: boolean } | null>(null);
  const [hiddenAttachments, setHiddenAttachments] = useState<Set<string>>(() => {
    const saved = localStorage.getItem('hiddenAttachments');
    return saved ? new Set(JSON.parse(saved)) : new Set();
  });
  const [lightbox, setLightbox] = useState<{ url: string; filename: string } | null>(null);
  const [highlightedMessageId, setHighlightedMessageId] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const messagesContainerRef = useRef<HTMLDivElement>(null);
  const messageRefs = useRef<{ [key: string]: HTMLDivElement | null }>({});
  const contextMenuRef = useRef<HTMLDivElement>(null);
  const editInputRef = useRef<HTMLInputElement>(null);
  const emojiPickerRef = useRef<HTMLDivElement>(null);
  const attachmentMenuRef = useRef<HTMLDivElement>(null);

  const currentUser = JSON.parse(localStorage.getItem('user') || '{}');

  // Ref     
  const processedInitialMessage = useRef<string>('');

  //    ()
  useEffect(() => {
    if (initialMessage && initialMessage !== processedInitialMessage.current) {
      processedInitialMessage.current = initialMessage;
      setNewMessage(initialMessage);
      //        
      setTimeout(() => {
        if (inputRef.current) {
          inputRef.current.focus();
          inputRef.current.setSelectionRange(initialMessage.length, initialMessage.length);
        }
        //       ( )
        if (onInitialMessageUsed) {
          onInitialMessageUsed();
        }
      }, 150);
    }
  }, [initialMessage]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    loadMessages();

    //   WebSocket
    const token = localStorage.getItem('token') || '';
    websocketService.connect(channel.id, token);

    //  
    websocketService.onMessage((wsMessage) => {
      if (wsMessage.type === 'message') {
        const payload = wsMessage.payload as WSMessagePayload;
        if (payload.channel_id === channel.id) {
          const msg = payload.message;
          setMessages((prev) => {
            const messageExists = prev.some(m => m.id === msg.id);
            if (messageExists) {
              return prev;
            }
            if (msg.user_id !== currentUser.id) {
              const username = msg.user?.username || 'Unknown';
              debugLogger.chat(`${username}: ${msg.content.substring(0, 50)}${msg.content.length > 50 ? '...' : ''}`);
            }
            return [...prev, msg];
          });
        }
      } else if (wsMessage.type === 'message-updated') {
        const payload = wsMessage.payload as WSMessagePayload;
        if (payload.channel_id === channel.id) {
          setMessages((prev) =>
            prev.map(m => m.id === payload.message.id ? payload.message : m)
          );
        }
      } else if (wsMessage.type === 'message-deleted') {
        const payload = wsMessage.payload as WSMessageDeletedPayload;
        if (payload.channel_id === channel.id) {
          setMessages((prev) => prev.filter(m => m.id !== payload.message_id));
        }
      } else if (wsMessage.type === 'reaction-updated') {
        const payload = wsMessage.payload as WSReactionUpdatedPayload;
        if (payload.channel_id === channel.id) {
          setMessages((prev) =>
            prev.map(m => m.id === payload.message_id
              ? { ...m, reactions: payload.reactions }
              : m
            )
          );
        }
      }
    });

    return () => {
      websocketService.disconnect();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [channel.id]);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  //   , emoji picker       
  useEffect(() => {
    const handleClickOutside = (e: MouseEvent) => {
      if (contextMenuRef.current && !contextMenuRef.current.contains(e.target as Node)) {
        setContextMenu(null);
      }
      if (emojiPickerRef.current && !emojiPickerRef.current.contains(e.target as Node)) {
        setEmojiPicker(null);
      }
      if (attachmentMenuRef.current && !attachmentMenuRef.current.contains(e.target as Node)) {
        setAttachmentMenu(null);
      }
    };

    if (contextMenu || emojiPicker || attachmentMenu) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [contextMenu, emojiPicker, attachmentMenu]);

  //     localStorage
  useEffect(() => {
    localStorage.setItem('hiddenAttachments', JSON.stringify(Array.from(hiddenAttachments)));
  }, [hiddenAttachments]);

  //    
  useEffect(() => {
    if (editingMessageId && editInputRef.current) {
      editInputRef.current.focus();
    }
  }, [editingMessageId]);

  const loadMessages = async () => {
    try {
      const data = await getMessages(channel.id);
      setMessages(data || []);
    } catch (error) {
      console.error('Failed to load messages:', error);
    }
  };

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  //    
  const scrollToMessage = (messageId: string) => {
    const messageElement = messageRefs.current[messageId];
    if (messageElement) {
      messageElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
      //  
      setHighlightedMessageId(messageId);
      setTimeout(() => setHighlightedMessageId(null), 2000);
    }
  };

  //    lightbox
  const openLightbox = (url: string, filename: string) => {
    setLightbox({ url: getFileUrl(url), filename });
  };

  //  lightbox
  const closeLightbox = () => {
    setLightbox(null);
  };

  // - textarea
  const autoResizeTextarea = () => {
    const textarea = inputRef.current;
    if (!textarea) return;

    textarea.style.height = 'auto';
    const maxHeight = window.innerHeight * 0.5; //   
    const newHeight = Math.min(textarea.scrollHeight, maxHeight);
    textarea.style.height = `${newHeight}px`;
  };

  const handleSendMessage = async (e?: React.FormEvent) => {
    if (e) e.preventDefault();
    if ((!newMessage.trim() && !attachedFile) || loading || uploading) return;

    const messageContent = newMessage.trim();
    setLoading(true);

    try {
      let attachmentUrl: string | undefined;

      //    
      if (attachedFile) {
        setUploading(true);
        try {
          const uploadResult = await uploadFile(attachedFile, setUploadProgress);
          attachmentUrl = uploadResult.url;
        } catch (uploadError) {
          console.error('Failed to upload file:', uploadError);
          debugLogger.error('  ');
          setUploading(false);
          setLoading(false);
          return;
        }
        setUploading(false);
        setUploadProgress(0);
      }

      await sendMessage(channel.id, messageContent || '', replyingTo?.id, attachmentUrl);
      debugLogger.chat(`: ${messageContent.substring(0, 50)}${messageContent.length > 50 ? '...' : ''}${attachmentUrl ? ' []' : ''}`);
      setNewMessage('');
      setReplyingTo(null);
      setAttachedFile(null);
      //   textarea
      if (inputRef.current) {
        inputRef.current.style.height = 'auto';
      }
      //       
      setTimeout(() => {
        inputRef.current?.focus();
      }, 0);
    } catch (error) {
      console.error('Failed to send message:', error);
      debugLogger.error('  ');
    } finally {
      setLoading(false);
    }
  };

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    if (file.size > MAX_FILE_SIZE) {
      alert('  .  : 10 ');
      return;
    }

    setAttachedFile(file);
    //  input        
    e.target.value = '';
  };

  const handleRemoveFile = () => {
    setAttachedFile(null);
  };

  //      (Ctrl+V)
  const handlePaste = (e: React.ClipboardEvent) => {
    const items = e.clipboardData?.items;
    if (!items) return;

    for (let i = 0; i < items.length; i++) {
      const item = items[i];
      if (item.type.startsWith('image/')) {
        e.preventDefault();
        const blob = item.getAsFile();
        if (blob) {
          if (blob.size > MAX_FILE_SIZE) {
            alert('  .  : 10 ');
            return;
          }
          //       
          const timestamp = new Date().toISOString().replace(/[:.]/g, '-');
          const ext = item.type.split('/')[1] || 'png';
          const file = new File([blob], `screenshot-${timestamp}.${ext}`, { type: item.type });
          setAttachedFile(file);
          debugLogger.info('    ');
        }
        return;
      }
    }
  };

  const handleInputKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    // Ctrl+B -  
    if (e.ctrlKey && e.key === 'b') {
      e.preventDefault();
      const textarea = inputRef.current;
      if (!textarea) return;

      const start = textarea.selectionStart;
      const end = textarea.selectionEnd;
      const text = newMessage;

      if (start !== end) {
        //    -  
        const selectedText = text.substring(start, end);
        const newText = text.substring(0, start) + '**' + selectedText + '**' + text.substring(end);
        setNewMessage(newText);
        //     **
        setTimeout(() => {
          textarea.setSelectionRange(end + 4, end + 4);
        }, 0);
      } else {
        //   -  ****    
        const newText = text.substring(0, start) + '****' + text.substring(end);
        setNewMessage(newText);
        setTimeout(() => {
          textarea.setSelectionRange(start + 2, start + 2);
        }, 0);
      }
      return;
    }

    // Enter  Shift -  
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
      return;
    }
    // Shift+Enter -   (  textarea)
  };

  const handleContextMenu = (e: React.MouseEvent, messageId: string, messageUserId: string) => {
    //       
    if (messageUserId !== currentUser.id) return;

    e.preventDefault();
    setContextMenu({ x: e.clientX, y: e.clientY, messageId });
  };

  const handleStartEdit = (message: Message) => {
    setEditingMessageId(message.id);
    setEditingContent(message.content);
    setContextMenu(null);
  };

  const handleCancelEdit = () => {
    setEditingMessageId(null);
    setEditingContent('');
  };

  const handleSaveEdit = async () => {
    if (!editingMessageId || !editingContent.trim()) return;

    try {
      await editMessage(channel.id, editingMessageId, editingContent.trim());
      setEditingMessageId(null);
      setEditingContent('');
    } catch (error) {
      console.error('Failed to edit message:', error);
      debugLogger.error('  ');
    }
  };

  const handleDeleteMessage = async (messageId: string) => {
    setContextMenu(null);
    try {
      await deleteMessage(channel.id, messageId);
    } catch (error) {
      console.error('Failed to delete message:', error);
      debugLogger.error('  ');
    }
  };

  const handleEditKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Escape') {
      handleCancelEdit();
    } else if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSaveEdit();
    }
  };

  const handleAddReaction = async (messageId: string, emoji: string) => {
    setEmojiPicker(null);
    try {
      await addReaction(channel.id, messageId, emoji);
    } catch (error) {
      console.error('Failed to add reaction:', error);
      debugLogger.error('  ');
    }
  };

  const handleToggleReaction = async (messageId: string, emoji: string, userIds: string[]) => {
    try {
      if (userIds.includes(currentUser.id)) {
        await removeReaction(channel.id, messageId, emoji);
      } else {
        await addReaction(channel.id, messageId, emoji);
      }
    } catch (error) {
      console.error('Failed to toggle reaction:', error);
      debugLogger.error('  ');
    }
  };

  const formatTime = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleTimeString('ru-RU', { hour: '2-digit', minute: '2-digit' });
  };

  const isEdited = (message: Message) => {
    return message.updated_at !== message.created_at;
  };

  //  URL    Electron
  const getFileUrl = (url: string) => {
    if (url.startsWith('http')) return url;
    const isElectron = window.location.protocol === 'file:' || !!(window as any).electronAPI;
    if (isElectron) {
      return `https://web.r1gate.ru${url}`;
    }
    return url;
  };

  //     
  const getAttachmentInfo = (attachmentUrl: string): { type: string; label: string; isMedia: boolean } => {
    const filename = attachmentUrl.split('/').pop() || 'file';
    const ext = filename.split('.').pop()?.toLowerCase() || '';

    if (['jpg', 'jpeg', 'png', 'webp'].includes(ext)) {
      return { type: 'photo', label: '', isMedia: true };
    }
    if (ext === 'gif') {
      return { type: 'gif', label: 'GIF', isMedia: true };
    }
    if (['mp4', 'webm', 'mov'].includes(ext)) {
      return { type: 'video', label: '', isMedia: true };
    }
    if (ext === 'pdf') {
      return { type: 'pdf', label: 'PDF', isMedia: false };
    }
    return { type: 'file', label: '', isMedia: false };
  };

  //     
  const renderReplyAttachmentPreview = (attachmentUrl: string) => {
    const url = getFileUrl(attachmentUrl);
    const info = getAttachmentInfo(attachmentUrl);

    if (info.isMedia) {
      return (
        <div style={styles.replyAttachmentPreview}>
          <img src={url} alt={info.label} style={styles.replyAttachmentThumb} />
        </div>
      );
    }

    //    
    const icons: { [key: string]: string } = {
      pdf: '',
      file: '',
    };

    return (
      <div style={styles.replyAttachmentIcon}>
        {icons[info.type] || ''}
      </div>
    );
  };

  //    
  const handleAttachmentMenuOpen = (e: React.MouseEvent, messageId: string, url: string, isOwner: boolean) => {
    e.preventDefault();
    e.stopPropagation();
    setAttachmentMenu({ x: e.clientX, y: e.clientY, messageId, url, isOwner });
  };

  const handleOpenInBrowser = (url: string) => {
    window.open(getFileUrl(url), '_blank');
    setAttachmentMenu(null);
  };

  const handleDownloadAttachment = async (url: string) => {
    const fullUrl = getFileUrl(url);
    const filename = url.split('/').pop() || 'file';

    try {
      const response = await fetch(fullUrl);
      const blob = await response.blob();
      const blobUrl = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = blobUrl;
      link.download = filename;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(blobUrl);
    } catch (error) {
      console.error('Failed to download:', error);
      // Fallback:    
      window.open(fullUrl, '_blank');
    }
    setAttachmentMenu(null);
  };

  const handleCopyAttachmentUrl = async (url: string) => {
    const fullUrl = getFileUrl(url);
    try {
      await navigator.clipboard.writeText(fullUrl);
      debugLogger.info(' ');
    } catch (error) {
      console.error('Failed to copy:', error);
    }
    setAttachmentMenu(null);
  };

  const handleHideAttachment = (url: string) => {
    setHiddenAttachments(prev => new Set(Array.from(prev).concat(url)));
    setAttachmentMenu(null);
  };

  const handleShowAttachment = (url: string) => {
    setHiddenAttachments(prev => {
      const newSet = new Set(prev);
      newSet.delete(url);
      return newSet;
    });
  };

  const handleReplyToAttachment = (messageId: string) => {
    const message = messages.find(m => m.id === messageId);
    if (message) {
      setReplyingTo(message);
      inputRef.current?.focus();
    }
    setAttachmentMenu(null);
  };

  //   
  const renderAttachment = (attachmentUrl: string, messageId: string, messageUserId: string, showMenu: boolean) => {
    const url = getFileUrl(attachmentUrl);
    const filename = attachmentUrl.split('/').pop() || 'file';
    const ext = filename.split('.').pop()?.toLowerCase() || '';
    const isOwner = messageUserId === currentUser.id;
    const isHidden = hiddenAttachments.has(attachmentUrl);

    //   ( ) -     
    const menuButton = (
      <button
        style={{
          ...styles.attachmentMenuBtn,
          opacity: showMenu ? 1 : 0,
        }}
        onClick={(e) => handleAttachmentMenuOpen(e, messageId, attachmentUrl, isOwner)}
        title=""
      >
        <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
          <circle cx="12" cy="5" r="2"/>
          <circle cx="12" cy="12" r="2"/>
          <circle cx="12" cy="19" r="2"/>
        </svg>
      </button>
    );

    //  
    if (isHidden) {
      return (
        <div style={styles.messageAttachment}>
          <div style={styles.hiddenAttachment}>
            <span style={styles.hiddenAttachmentText}> </span>
            <button
              style={styles.showAttachmentBtn}
              onClick={() => handleShowAttachment(attachmentUrl)}
            >
              
            </button>
          </div>
        </div>
      );
    }

    // 
    if (['jpg', 'jpeg', 'png', 'gif', 'webp'].includes(ext)) {
      return (
        <div style={styles.messageAttachment}>
          <div style={styles.attachmentWrapper}>
            <img
              src={url}
              alt="Attachment"
              style={styles.messageAttachmentImage}
              onClick={() => openLightbox(attachmentUrl, filename)}
            />
            {menuButton}
          </div>
        </div>
      );
    }

    // 
    if (['mp4', 'webm', 'mov'].includes(ext)) {
      return (
        <div style={styles.messageAttachment}>
          <div style={styles.attachmentWrapper}>
            <video
              src={url}
              controls
              style={styles.messageAttachmentVideo}
            />
            {menuButton}
          </div>
        </div>
      );
    }

    //  
    return (
      <div style={styles.messageAttachment}>
        <div style={styles.attachmentWrapper}>
          <a href={url} target="_blank" rel="noopener noreferrer" style={styles.messageAttachmentFile}>
            <span style={styles.messageAttachmentFileIcon}></span>
            <span style={styles.messageAttachmentFileName}>{filename}</span>
          </a>
          {menuButton}
        </div>
      </div>
    );
  };

  //      ,     
  const renderMessageContent = (content: string) => {
    //      
    const lines = content.split('\n');

    return lines.map((line, lineIndex) => {
      //    
      const combinedRegex = /(@[\w\u0400-\u04FF--]+|\*\*(.+?)\*\*)/g;
      const parts: (string | JSX.Element)[] = [];
      let lastIndex = 0;
      let match;

      while ((match = combinedRegex.exec(line)) !== null) {
        //    
        if (match.index > lastIndex) {
          parts.push(line.slice(lastIndex, match.index));
        }

        const fullMatch = match[0];

        if (fullMatch.startsWith('@')) {
          // 
          const username = fullMatch.slice(1);
          const mentionKey = `mention-${lineIndex}-${match.index}`;
          parts.push(
            <span
              key={mentionKey}
              style={styles.mention}
              onClick={(e) => {
                e.stopPropagation();
                if (onUserClick) {
                  const user: User = {
                    id: username,
                    username: username,
                    email: '',
                    created_at: new Date().toISOString(),
                    updated_at: new Date().toISOString(),
                  };
                  const rect = (e.currentTarget as HTMLElement).getBoundingClientRect();
                  onUserClick(user, { x: rect.left, y: rect.bottom + 5 });
                }
              }}
            >
              @{username}
            </span>
          );
        } else if (fullMatch.startsWith('**') && fullMatch.endsWith('**')) {
          //  
          const boldText = match[2];
          parts.push(
            <strong key={`bold-${lineIndex}-${match.index}`} style={{ fontWeight: 700 }}>
              {boldText}
            </strong>
          );
        }

        lastIndex = match.index + fullMatch.length;
      }

      //   
      if (lastIndex < line.length) {
        parts.push(line.slice(lastIndex));
      }

      //     ( )
      return (
        <React.Fragment key={`line-${lineIndex}`}>
          {parts.length > 0 ? parts : line}
          {lineIndex < lines.length - 1 && <br />}
        </React.Fragment>
      );
    });
  };

  const handleUserClick = (message: Message, e: React.MouseEvent) => {
    //     
    if (message.user_id === currentUser.id) return;
    if (!onUserClick || !message.user) return;

    const rect = (e.currentTarget as HTMLElement).getBoundingClientRect();
    //      (300px   + 10px )
    onUserClick(message.user, { x: rect.left - 310, y: rect.top });
  };

  return (
    <div style={styles.container}>
      {/*   */}
      <div style={styles.header}>
        <h3># {channel.name}</h3>
      </div>

      {/*  */}
      <div style={styles.messages} ref={messagesContainerRef}>
        {messages.length === 0 ? (
          <div style={styles.emptyState}>
            <p>   .  !</p>
          </div>
        ) : (
          messages.map((message, index) => {
            // ,        
            const prevMessage = index > 0 ? messages[index - 1] : null;
            const isGrouped = prevMessage && prevMessage.user_id === message.user_id;
            const isHovered = hoveredMessageId === message.id;

            const isHighlighted = highlightedMessageId === message.id;

            return (
            <div
              key={message.id}
              ref={(el) => { messageRefs.current[message.id] = el; }}
              style={{
                ...styles.message,
                ...(isGrouped ? styles.messageGrouped : {}),
                backgroundColor: isHighlighted
                  ? 'var(--accent-soft)'
                  : editingMessageId === message.id
                    ? 'var(--bg-modifier-selected)'
                    : isHovered
                      ? 'var(--bg-modifier-hover)'
                      : undefined,
                transition: 'background-color 0.3s ease',
              }}
              onMouseEnter={() => setHoveredMessageId(message.id)}
              onMouseLeave={() => setHoveredMessageId(null)}
              onContextMenu={(e) => handleContextMenu(e, message.id, message.user_id)}
            >
              {/*   -      */}
              {isHovered && editingMessageId !== message.id && (
                <div style={styles.messageActions}>
                  <button
                    style={styles.messageActionBtn}
                    onClick={() => setEmojiPicker({ messageId: message.id })}
                    title=" "
                  >
                    <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                      <circle cx="12" cy="12" r="10"/>
                      <path d="M8 14s1.5 2 4 2 4-2 4-2"/>
                      <line x1="9" y1="9" x2="9.01" y2="9"/>
                      <line x1="15" y1="9" x2="15.01" y2="9"/>
                    </svg>
                  </button>
                  <button
                    style={styles.messageActionBtn}
                    onClick={() => {
                      setReplyingTo(message);
                      inputRef.current?.focus();
                    }}
                    title=""
                  >
                    <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                      <polyline points="9 17 4 12 9 7"/>
                      <path d="M20 18v-2a4 4 0 0 0-4-4H4"/>
                    </svg>
                  </button>
                  {message.user_id === currentUser.id && (
                    <>
                      <button
                        style={styles.messageActionBtn}
                        onClick={() => handleStartEdit(message)}
                        title=""
                      >
                        <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                          <path d="M17 3a2.85 2.85 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"/>
                        </svg>
                      </button>
                      <button
                        style={{...styles.messageActionBtn, color: '#ff4466', filter: 'drop-shadow(0 0 3px rgba(255, 68, 102, 0.5))'}}
                        onClick={() => handleDeleteMessage(message.id)}
                        title=""
                      >
                        <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                          <polyline points="3 6 5 6 21 6"/>
                          <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"/>
                        </svg>
                      </button>
                    </>
                  )}
                </div>
              )}
              {!isGrouped ? (
                <div
                  style={{
                    ...styles.messageAvatar,
                    backgroundColor: getAvatarColor(message.user?.id || message.user?.username || 'user'),
                    cursor: message.user_id !== currentUser.id ? 'pointer' : 'default',
                  }}
                  onClick={(e) => handleUserClick(message, e)}
                >
                  {message.user?.username?.[0]?.toUpperCase() || '?'}
                </div>
              ) : (
                <div style={styles.messageAvatarPlaceholder}>
                  <span style={{...styles.groupedTime, opacity: isHovered ? 1 : 0}}>{formatTime(message.created_at)}</span>
                </div>
              )}
              <div style={styles.messageContent}>
                {/* ,      */}
                {message.reply_to && (
                  <div
                    style={styles.replyPreview}
                    onClick={() => scrollToMessage(message.reply_to!.id)}
                    title="  "
                  >
                    <div style={styles.replyLine} />
                    {message.reply_to.attachment_url ? (
                      <>
                        {renderReplyAttachmentPreview(message.reply_to.attachment_url)}
                        <div style={styles.replyAttachmentInfo}>
                          <span style={styles.replyAuthor}>{message.reply_to.user?.username}</span>
                          <span style={styles.replyAttachmentType}>
                            {getAttachmentInfo(message.reply_to.attachment_url).label}
                          </span>
                        </div>
                      </>
                    ) : (
                      <>
                        <span style={styles.replyAuthor}>{message.reply_to.user?.username}</span>
                        <span style={styles.replyText}>
                          {message.reply_to.content.length > 50
                            ? message.reply_to.content.substring(0, 50) + '...'
                            : message.reply_to.content}
                        </span>
                      </>
                    )}
                  </div>
                )}
                {!isGrouped && (
                <div style={styles.messageHeader}>
                  <span
                    style={{
                      ...styles.messageAuthor,
                      cursor: message.user_id !== currentUser.id ? 'pointer' : 'default',
                    }}
                    onClick={(e) => handleUserClick(message, e)}
                  >{message.user?.username}</span>
                  <span style={styles.messageTime}>
                    {formatTime(message.created_at)}
                    {isEdited(message) && <span style={styles.editedLabel}> (.)</span>}
                  </span>
                </div>
                )}
                {editingMessageId === message.id ? (
                  <div style={styles.editContainer}>
                    <input
                      ref={editInputRef}
                      type="text"
                      value={editingContent}
                      onChange={(e) => setEditingContent(e.target.value)}
                      onKeyDown={handleEditKeyDown}
                      style={styles.editInput}
                    />
                    <div style={styles.editActions}>
                      <span style={styles.editHint}>Escape    Enter  </span>
                    </div>
                  </div>
                ) : (
                  <>
                    {message.content && (
                      <div style={styles.messageText}>{renderMessageContent(message.content)}</div>
                    )}
                    {/*  */}
                    {message.attachment_url && renderAttachment(message.attachment_url, message.id, message.user_id, isHovered)}
                    {/*  -    */}
                    {message.reactions && message.reactions.length > 0 && (
                      <div style={styles.reactionsContainer}>
                        {message.reactions.map((reaction) => (
                          <button
                            key={reaction.emoji}
                            style={{
                              ...styles.reactionBadge,
                              backgroundColor: reaction.user_ids.includes(currentUser.id)
                                ? 'var(--bg-accent)'
                                : 'var(--bg-modifier-hover)',
                            }}
                            onClick={() => handleToggleReaction(message.id, reaction.emoji, reaction.user_ids)}
                            title={`${reaction.count} ${reaction.count === 1 ? '' : ''}`}
                          >
                            {reaction.emoji} {reaction.count}
                          </button>
                        ))}
                      </div>
                    )}
                  </>
                )}
              </div>
            </div>
          );})
        )}
        <div ref={messagesEndRef} />
      </div>

      {/*   */}
      {contextMenu && (
        <div
          ref={contextMenuRef}
          style={{
            ...styles.contextMenu,
            left: contextMenu.x,
            top: contextMenu.y,
          }}
        >
          <button
            style={styles.contextMenuItem}
            onClick={() => {
              const msg = messages.find(m => m.id === contextMenu.messageId);
              if (msg) handleStartEdit(msg);
            }}
          >
            <svg style={styles.contextMenuIcon} width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <path d="M17 3a2.85 2.85 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"/>
            </svg>
            
          </button>
          <button
            style={{ ...styles.contextMenuItem, color: '#ff4466' }}
            onClick={() => handleDeleteMessage(contextMenu.messageId)}
          >
            <svg style={{color: '#ff4466', filter: 'drop-shadow(0 0 2px rgba(255, 68, 102, 0.4))'}} width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <polyline points="3 6 5 6 21 6"/>
              <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"/>
            </svg>
            
          </button>
        </div>
      )}

      {/* Emoji Picker */}
      {emojiPicker && (
        <div
          ref={emojiPickerRef}
          style={styles.emojiPicker}
        >
          <div style={styles.emojiPickerTitle}> </div>
          <div style={styles.emojiGrid}>
            {COMMON_EMOJIS.map((emoji) => (
              <button
                key={emoji}
                style={styles.emojiButton}
                onClick={() => handleAddReaction(emojiPicker.messageId, emoji)}
              >
                {emoji}
              </button>
            ))}
          </div>
        </div>
      )}

      {/*   */}
      {attachmentMenu && (
        <div
          ref={attachmentMenuRef}
          style={{
            ...styles.attachmentContextMenu,
            left: attachmentMenu.x,
            top: attachmentMenu.y,
          }}
        >
          <button
            style={styles.contextMenuItem}
            onClick={() => handleReplyToAttachment(attachmentMenu.messageId)}
          >
            <svg style={styles.contextMenuIcon} width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <polyline points="9 17 4 12 9 7"/>
              <path d="M20 18v-2a4 4 0 0 0-4-4H4"/>
            </svg>
            
          </button>
          <button
            style={styles.contextMenuItem}
            onClick={() => handleCopyAttachmentUrl(attachmentMenu.url)}
          >
            <svg style={styles.contextMenuIcon} width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <rect x="9" y="9" width="13" height="13" rx="2" ry="2"/>
              <path d="M5 15H4a2 2 0 0 1-2-2V4a2 2 0 0 1 2-2h9a2 2 0 0 1 2 2v1"/>
            </svg>
             
          </button>
          <button
            style={styles.contextMenuItem}
            onClick={() => handleOpenInBrowser(attachmentMenu.url)}
          >
            <svg style={styles.contextMenuIcon} width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <path d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6"/>
              <polyline points="15 3 21 3 21 9"/>
              <line x1="10" y1="14" x2="21" y2="3"/>
            </svg>
              
          </button>
          <button
            style={styles.contextMenuItem}
            onClick={() => handleDownloadAttachment(attachmentMenu.url)}
          >
            <svg style={styles.contextMenuIcon} width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4"/>
              <polyline points="7 10 12 15 17 10"/>
              <line x1="12" y1="15" x2="12" y2="3"/>
            </svg>
            
          </button>
          <button
            style={styles.contextMenuItem}
            onClick={() => handleHideAttachment(attachmentMenu.url)}
          >
            <svg style={styles.contextMenuIcon} width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <path d="M17.94 17.94A10.07 10.07 0 0 1 12 20c-7 0-11-8-11-8a18.45 18.45 0 0 1 5.06-5.94M9.9 4.24A9.12 9.12 0 0 1 12 4c7 0 11 8 11 8a18.5 18.5 0 0 1-2.16 3.19m-6.72-1.07a3 3 0 1 1-4.24-4.24"/>
              <line x1="1" y1="1" x2="23" y2="23"/>
            </svg>
            
          </button>
          {attachmentMenu.isOwner && (
            <button
              style={{ ...styles.contextMenuItem, color: '#ff4466' }}
              onClick={() => {
                handleDeleteMessage(attachmentMenu.messageId);
                setAttachmentMenu(null);
              }}
            >
              <svg style={{color: '#ff4466', filter: 'drop-shadow(0 0 2px rgba(255, 68, 102, 0.4))'}} width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <polyline points="3 6 5 6 21 6"/>
                <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"/>
              </svg>
              
            </button>
          )}
        </div>
      )}

      {/*   */}
      <div style={styles.inputContainer}>
        {/*   */}
        {replyingTo && (
          <div style={styles.replyIndicator}>
            {replyingTo.attachment_url ? (
              <>
                {renderReplyAttachmentPreview(replyingTo.attachment_url)}
                <div style={styles.replyIndicatorContent}>
                  <span style={styles.replyIndicatorAuthor}>{replyingTo.user?.username}</span>
                  <span style={styles.replyAttachmentType}>
                    {getAttachmentInfo(replyingTo.attachment_url).label}
                  </span>
                </div>
              </>
            ) : (
              <div style={styles.replyIndicatorContentText}>
                <span style={styles.replyIndicatorLabel}> </span>
                <span style={styles.replyIndicatorAuthor}>{replyingTo.user?.username}</span>
                <span style={styles.replyIndicatorText}>
                  {replyingTo.content.length > 60
                    ? replyingTo.content.substring(0, 60) + '...'
                    : replyingTo.content}
                </span>
              </div>
            )}
            <button
              style={styles.replyIndicatorClose}
              onClick={() => setReplyingTo(null)}
              title=" "
            >
              
            </button>
          </div>
        )}
        {/*    */}
        {attachedFile && (
          <div style={styles.attachmentPreview}>
            <div style={styles.attachmentInfo}>
              {attachedFile.type.startsWith('image/') ? (
                <img
                  src={URL.createObjectURL(attachedFile)}
                  alt="Preview"
                  style={styles.attachmentThumb}
                />
              ) : (
                <div style={styles.attachmentIcon}></div>
              )}
              <div style={styles.attachmentDetails}>
                <span style={styles.attachmentName}>{attachedFile.name}</span>
                <span style={styles.attachmentSize}>
                  {(attachedFile.size / 1024).toFixed(1)} 
                </span>
              </div>
            </div>
            <button
              onClick={handleRemoveFile}
              style={styles.removeAttachment}
              title=" "
            >
              
            </button>
            {uploading && (
              <div style={styles.uploadProgress}>
                <div style={{ ...styles.uploadProgressBar, width: `${uploadProgress}%` }} />
              </div>
            )}
          </div>
        )}
        <form onSubmit={handleSendMessage} style={styles.form}>
          <input
            type="file"
            ref={fileInputRef}
            onChange={handleFileSelect}
            style={{ display: 'none' }}
            accept="image/*,video/*,.pdf,.txt,.zip,.rar,.7z,.gz"
          />
          <div style={styles.inputWrapper}>
            <button
              type="button"
              onClick={() => fileInputRef.current?.click()}
              style={styles.attachButton}
              title=" "
              disabled={uploading}
            >
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M21.44 11.05l-9.19 9.19a6 6 0 0 1-8.49-8.49l9.19-9.19a4 4 0 0 1 5.66 5.66l-9.2 9.19a2 2 0 0 1-2.83-2.83l8.49-8.48" />
              </svg>
            </button>
            <textarea
              ref={inputRef}
              value={newMessage}
              onChange={(e) => {
                setNewMessage(e.target.value);
                autoResizeTextarea();
              }}
              onKeyDown={handleInputKeyDown}
              onPaste={handlePaste}
              placeholder={replyingTo ? ` ${replyingTo.user?.username}...` : `  #${channel.name}`}
              style={styles.textarea}
              disabled={loading || uploading}
              rows={1}
            />
            <button
              type="submit"
              style={{
                ...styles.sendButton,
                ...((newMessage.trim() || attachedFile) && !loading && !uploading ? {
                  color: '#00ff88',
                  filter: sendButtonHovered
                    ? 'drop-shadow(0 0 12px rgba(0, 255, 136, 0.8))'
                    : 'drop-shadow(0 0 6px rgba(0, 255, 136, 0.5))',
                  transform: sendButtonActive
                    ? 'scale(1.15)'
                    : sendButtonHovered
                      ? 'scale(1.08)'
                      : 'scale(1)',
                } : {
                  color: 'var(--text-faded)',
                  filter: 'none',
                  transform: 'scale(1)',
                }),
                cursor: (newMessage.trim() || attachedFile) && !loading && !uploading ? 'pointer' : 'default',
              }}
              disabled={(!newMessage.trim() && !attachedFile) || loading || uploading}
              onMouseEnter={() => setSendButtonHovered(true)}
              onMouseLeave={() => {
                setSendButtonHovered(false);
                setSendButtonActive(false);
              }}
              onMouseDown={() => setSendButtonActive(true)}
              onMouseUp={() => setSendButtonActive(false)}
            >
              <svg width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <path d="M22 2L11 13"/>
                <path d="M22 2L15 22L11 13L2 9L22 2Z"/>
              </svg>
            </button>
          </div>
        </form>
      </div>

      {/* Lightbox    */}
      {lightbox && (
        <div style={styles.lightboxOverlay} onClick={closeLightbox}>
          <div style={styles.lightboxContent} onClick={(e) => e.stopPropagation()}>
            <img src={lightbox.url} alt={lightbox.filename} style={styles.lightboxImage} />
            <div style={styles.lightboxFilename}>{lightbox.filename}</div>
          </div>
          <button style={styles.lightboxClose} onClick={closeLightbox} title="">
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <line x1="18" y1="6" x2="6" y2="18"/>
              <line x1="6" y1="6" x2="18" y2="18"/>
            </svg>
          </button>
          <div style={styles.lightboxActions}>
            <button
              style={styles.lightboxActionBtn}
              onClick={(e) => {
                e.stopPropagation();
                window.open(lightbox.url, '_blank');
              }}
              title="  "
            >
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6"/>
                <polyline points="15 3 21 3 21 9"/>
                <line x1="10" y1="14" x2="21" y2="3"/>
              </svg>
            </button>
            <button
              style={styles.lightboxActionBtn}
              onClick={async (e) => {
                e.stopPropagation();
                try {
                  const response = await fetch(lightbox.url);
                  const blob = await response.blob();
                  const blobUrl = URL.createObjectURL(blob);
                  const link = document.createElement('a');
                  link.href = blobUrl;
                  link.download = lightbox.filename;
                  document.body.appendChild(link);
                  link.click();
                  document.body.removeChild(link);
                  URL.revokeObjectURL(blobUrl);
                } catch {
                  window.open(lightbox.url, '_blank');
                }
              }}
              title=""
            >
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4"/>
                <polyline points="7 10 12 15 17 10"/>
                <line x1="12" y1="15" x2="12" y2="3"/>
              </svg>
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

