/**
 * Centralized configuration for API and media URLs
 *
 * IMPORTANT: All API services must use these helpers to ensure
 * proper URL handling in Electron (file:// protocol)
 */

const WEB_BASE_URL = 'https://web.rgconnect.ru';

/**
 * Check if running in Electron app
 */
export const isElectronApp = (): boolean => {
  return window.location.protocol === 'file:' || !!(window as any).electronAPI;
};

/**
 * Get API base URL
 * - In Electron: absolute URL to web server
 * - In browser: relative /api path
 */
export const getApiUrl = (): string => {
  if (isElectronApp()) {
    return `${WEB_BASE_URL}/api`;
  }
  return process.env.REACT_APP_API_URL || '/api';
};

/**
 * Get WebSocket URL for a given path
 * - In Electron: absolute wss:// URL
 * - In browser: relative path with current host
 */
export const getWebSocketUrl = (path: string): string => {
  if (isElectronApp()) {
    return `wss://web.rgconnect.ru${path}`;
  }
  const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
  return `${protocol}//${window.location.host}${path}`;
};

/**
 * Convert relative media URL to absolute for Electron
 * Use this for: avatar_url, banner_url, image_url, file attachments
 *
 * @example
 * getMediaUrl('/uploads/avatars/123.png')
 * // In Electron: 'https://web.rgconnect.ru/uploads/avatars/123.png'
 * // In browser: '/uploads/avatars/123.png'
 */
export const getMediaUrl = (url: string | undefined | null): string | undefined => {
  if (!url) return undefined;

  // Already absolute URL or data URL
  if (url.startsWith('http://') || url.startsWith('https://') || url.startsWith('data:')) {
    return url;
  }

  // Relative URL in Electron - add base domain
  if (isElectronApp() && url.startsWith('/')) {
    return `${WEB_BASE_URL}${url}`;
  }

  return url;
};

/**
 * Transform all media URLs in API response data
 * Recursively processes objects and arrays
 */
export const transformMediaUrls = (data: any): any => {
  if (!data || !isElectronApp()) return data;

  if (Array.isArray(data)) {
    return data.map(transformMediaUrls);
  }

  if (typeof data === 'object') {
    const result = { ...data };
    const mediaKeys = ['avatar_url', 'banner_url', 'image_url', 'file_url', 'attachment_url'];

    for (const key of Object.keys(result)) {
      if (mediaKeys.includes(key) && typeof result[key] === 'string') {
        result[key] = getMediaUrl(result[key]);
      } else if (typeof result[key] === 'object') {
        result[key] = transformMediaUrls(result[key]);
      }
    }
    return result;
  }

  return data;
};

/**
 * Create axios response interceptor for media URL transformation
 */
export const createMediaUrlInterceptor = () => {
  return (response: any) => {
    response.data = transformMediaUrls(response.data);
    return response;
  };
};
