import React, { useState, useEffect } from 'react';
import '../types/electron.d';

interface TitleBarProps {
  title?: string;
}

const TitleBar: React.FC<TitleBarProps> = ({ title = 'RGConnect' }) => {
  const [isMaximized, setIsMaximized] = useState(false);
  const [hoveredButton, setHoveredButton] = useState<string | null>(null);
  const isElectron = !!window.electronAPI?.isElectron;

  // Check initial maximized state and listen for changes
  useEffect(() => {
    if (!isElectron) return;

    const checkMaximized = async () => {
      if (window.electronAPI?.windowIsMaximized) {
        const maximized = await window.electronAPI.windowIsMaximized();
        setIsMaximized(maximized);
      }
    };

    checkMaximized();

    // Listen for maximize/unmaximize events
    const cleanup = window.electronAPI?.onWindowMaximized?.((maximized) => {
      setIsMaximized(maximized);
    });

    return () => {
      cleanup?.();
    };
  }, [isElectron]);

  // Only render in Electron
  if (!isElectron) {
    return null;
  }

  const handleMinimize = () => {
    window.electronAPI?.windowMinimize();
  };

  const handleMaximize = () => {
    window.electronAPI?.windowMaximize();
  };

  const handleClose = () => {
    window.electronAPI?.windowClose();
  };

  return (
    <div style={styles.titleBar}>
      <div style={styles.dragRegion} />

      {/* Center - Logo and Title */}
      <div style={styles.centerSection}>
        <div style={styles.logoContainer}>
          <svg width="16" height="16" viewBox="0 0 100 100" fill="none">
            <circle cx="30" cy="35" r="10" fill="var(--primary)"/>
            <circle cx="70" cy="35" r="10" fill="var(--primary)"/>
            <circle cx="50" cy="70" r="10" fill="var(--primary)"/>
            <line x1="30" y1="35" x2="70" y2="35" stroke="var(--primary)" strokeWidth="4"/>
            <line x1="30" y1="35" x2="50" y2="70" stroke="var(--primary)" strokeWidth="4"/>
            <line x1="70" y1="35" x2="50" y2="70" stroke="var(--primary)" strokeWidth="4"/>
          </svg>
        </div>
        <div style={styles.title}>{title}</div>
      </div>

      {/* Right side - Window Controls */}
      <div style={styles.windowControls}>
        {/* Minimize */}
        <button
          style={{
            ...styles.controlButton,
            backgroundColor: hoveredButton === 'minimize' ? 'rgba(255, 255, 255, 0.08)' : 'transparent',
          }}
          onClick={handleMinimize}
          onMouseEnter={() => setHoveredButton('minimize')}
          onMouseLeave={() => setHoveredButton(null)}
          title=""
        >
          <svg width="12" height="12" viewBox="0 0 16 16" fill="currentColor">
            <rect x="3" y="7.5" width="10" height="1"/>
          </svg>
        </button>

        {/* Maximize/Restore */}
        <button
          style={{
            ...styles.controlButton,
            backgroundColor: hoveredButton === 'maximize' ? 'rgba(255, 255, 255, 0.08)' : 'transparent',
          }}
          onClick={handleMaximize}
          onMouseEnter={() => setHoveredButton('maximize')}
          onMouseLeave={() => setHoveredButton(null)}
          title={isMaximized ? '' : ''}
        >
          {isMaximized ? (
            // Restore icon (two overlapping squares)
            <svg width="12" height="12" viewBox="0 0 16 16" fill="currentColor">
              <path
                fill="none"
                stroke="currentColor"
                strokeWidth="1.2"
                d="M4,5 L4,12 L11,12 L11,5 L4,5 M5,5 L5,3 L13,3 L13,11 L11,11"
              />
            </svg>
          ) : (
            // Maximize icon (single square)
            <svg width="12" height="12" viewBox="0 0 16 16" fill="currentColor">
              <rect
                x="3" y="3"
                width="10" height="10"
                fill="none"
                stroke="currentColor"
                strokeWidth="1.2"
              />
            </svg>
          )}
        </button>

        {/* Close */}
        <button
          style={{
            ...styles.controlButton,
            ...styles.closeButton,
            backgroundColor: hoveredButton === 'close' ? '#E81123' : 'transparent',
          }}
          onClick={handleClose}
          onMouseEnter={() => setHoveredButton('close')}
          onMouseLeave={() => setHoveredButton(null)}
          title=""
        >
          <svg width="12" height="12" viewBox="0 0 16 16" fill="currentColor">
            <path
              d="M3.5 3.5l9 9m0-9l-9 9"
              stroke="currentColor"
              strokeWidth="1.5"
              strokeLinecap="round"
            />
          </svg>
        </button>
      </div>
    </div>
  );
};

const styles: { [key: string]: React.CSSProperties } = {
  titleBar: {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    height: '30px',
    backgroundColor: '#1A1A1A',
    borderBottom: '1px solid #2A2A2A',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: '0 8px',
    zIndex: 9999,
    userSelect: 'none',
  },
  dragRegion: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    WebkitAppRegion: 'drag',
  } as React.CSSProperties,
  centerSection: {
    position: 'absolute',
    left: '50%',
    transform: 'translateX(-50%)',
    display: 'flex',
    alignItems: 'center',
    gap: '8px',
    zIndex: 1,
    pointerEvents: 'none',
  } as React.CSSProperties,
  logoContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  title: {
    fontSize: '12px',
    fontWeight: 500,
    color: '#E0E0E0',
    letterSpacing: '0.3px',
  },
  windowControls: {
    position: 'absolute',
    right: 0,
    top: 0,
    display: 'flex',
    height: '30px',
    WebkitAppRegion: 'no-drag',
  } as React.CSSProperties,
  controlButton: {
    width: '40px',
    height: '30px',
    border: 'none',
    background: 'transparent',
    color: '#B3B3B3',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    cursor: 'pointer',
    transition: 'background-color 0.15s, color 0.15s',
  },
  closeButton: {
    // Close button styles handled inline with hover
  },
};

export default TitleBar;
